.content-tabs {
  &__header {
    position: relative;
    min-height: 250px;
    background-position: 10%;
    background-size: cover;
    background-repeat: no-repeat;
    &::before {
      content: "";
      background-color: #0000007a;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
    }
    h3 {
      z-index: 9;
      color: aliceblue;
      font-size: 40px;
    }
    p {
      z-index: 9;
      font-size: 30px;
      color: aliceblue;
    }
  }
  &__detail {
    padding: 20px;

    &__progress {
      margin: 30px 0px;
      h3 {
        color: #000000;
        flex: 1 1;
        font-size: 16px;
        text-shadow: 1px 0px 2px #0000008e;
        margin: 0;
      }
      .progress {
        background-color: #ffffffa6;
        border-radius: 10px;
        flex: 1 1;
        height: 20px;
      }
    }
  }
}

@media (max-width: 768px) {
  .content-tabs {
    &__header {
      h3 {
        font-size: 25px;
      }
      p {
        font-size: 20px;
      }
    }
    &__detail {
      padding: 5px;

      &__progress {
        margin: 20px 0px;
      }
    }
  }
}
